@import "../../share";
$p: 0.8rem;
// TABLE
.table {
  overflow: scroll;
  overflow-y: auto;
  overflow-x: auto;
  height: calc(100vh - 11rem);

  table {
    border-collapse: collapse;
    font-weight: 200;
    text-align: left;
    width: 100%;

    thead {
      background-color: $color-bg-primary;
      padding: 1rem;
      font-weight: bold;
      font-size: 95%;
      tr {
        border-bottom: $border-primary;

        &:focus-within,
        &:hover {
          color: $color-font-hover;
          cursor: pointer;
        }

        @include respond(tab-port) {
          display: flex;
        }
        td {
          padding: $p;

          @include respond(tab-port) {
            padding: $p;
            padding-left: 0;
            &#count,
            &#attach {
              display: none;
            }
            &:first-child {
              padding-left: $p;
            }
            &:not(:first-child):before {
              content: "|";
              padding-right: $p;
            }
          }
        }
      }
    }

    tbody {
      font-weight: 100;

      tr {
        border-bottom: $border-primary;
        @include respond(tab-port) {
          padding: 0.8rem;
          display: flex;
          flex-direction: column;
          display: grid;
          // grid-template-areas:
          //     "mi fr fr fr fr cl dt ai"
          //     "mi to to to to to to ct"
          //     "sj sj sj sj sj sj sj sj";
          grid-template-rows: 1fr 1fr 1fr;
          grid-template-columns: 1.2rem 1fr auto auto auto;
          align-items: center;
          justify-items: start;
        }

        &:focus-within,
        &:hover {
          color: $color-font-hover;
          cursor: pointer;
        }

        td {
          padding: $p;

          &.mailicon,
          &.arrowicon {
            display: none;
          }

          @include respond(tab-port) {
            padding: 0.2rem;
            &.mailicon {
              display: initial;
              width: 0.8rem;
              // grid-area: mi;
              grid-row: 1/3;
              grid-column: 1/2;
            }

            &.arrowicon {
              display: initial;
              width: 0.25rem;
              // grid-area: ai;
              grid-row: 1/2;
              grid-column: 5/6;
            }
            &.from {
              // grid-area: fr;
              font-weight: bold;
              font-size: smaller;
              grid-row: 1/2;
              grid-column: 2/3;
            }
            &.to {
              // grid-area: to;
              font-size: smaller;
              grid-row: 2/3;
              grid-column: 2/3;
            }
            &.date {
              // grid-area: dt;
              font-size: smaller;
              grid-row: 1/2;
              grid-column: 4/5;
              min-width: 10px;
              width: 100%;
              text-overflow: ellipsis;
              white-space: nowrap;
              overflow: hidden;
              z-index: -1;
            }
            &.subject {
              // grid-area: sj;
              grid-row: 3/4;
              grid-column: 1/-1;
              width: initial;
            }
            &.count {
              // grid-area: ct;
              grid-row: 2/3;
              grid-column: 4/5;

              font-size: smaller;
              justify-self: end;
            }
            &.clip {
              // grid-area: cl;
              grid-row: 1/2;
              grid-column: 3/4;
              justify-self: end;
            }
          }
        }
      }
    }
  }
}
