.dateinput {
    position: relative;
    background-color: mediumaquamarine;
    display: inline-block;
    border-radius: 7px;
    overflow: hidden;
    border: 1px solid lightgray;
    margin: 1rem 0;

    &__input {
        padding: 0.7rem 3rem;
        left: 0;
        top: 0;
        // z-index: 50;
        // background-color: mediumvioletred;
        border: none;
        outline: none;
        font-size: inherit;

        &:active,
        &:hover {
            border: none;
            outline: none;
        }
    }

    &__icon {
        height: 1.5rem;
        width: 1.5rem;
        // z-index: 51;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        padding: 1rem 0.7rem;

        &--search {
            @extend .dateinput__icon;
            right: 0;
            background-color: #f5f5f5;
            padding-left: .9rem;
            border-left: 1px solid #d3d3d3;
        }

        &--calendar {
            @extend .dateinput__icon;
            left: 0;
            // background-color: aqua;
            padding-left: 0.8rem;
        }
    }
}
