@import "../../share";

$p: 0.8rem;

.date {
  position: relative;
  @include respond(tab-port) {
    padding-left: $p;
  }
  .daterange {
    display: none;
    position: absolute;
    top: 4.5rem;
    left: 0rem;
    border: $border-primary;
    border-radius: 7px;
    &.show {
      display: inline-flex;
    }
    @include respond(tab-port) {
      margin-left: $p;
      transform: scale(0.9);
      transform-origin: left top;
    }
    @include respond(phone) {
      margin-left: $p;
      transform: scale(0.8);
      transform-origin: left top;
    }
  }
}
