@import "../../share";

$p: 0.8rem;

.tharrow {
    padding: 0;
    height: 0.4rem;
    &.desc {
        padding-left: 0;
        padding-right: $p;
        transform: rotate(180deg);
    }
    &.asc {
        padding-left: $p;
        padding-right: 0;
        transform: rotate(0deg);
    }
}
