@import "share";

body {
  font-family: Arial, Helvetica, sans-serif;
  color: $color-font-primary;
  padding: 1rem 2rem;

  @include respond(tab-port) {
    padding: 1rem 0;
  }

  @include respond(tab-land) {
    font-size: 0.95rem;
  }
}

.copy {
  position: absolute;
  bottom: 0;
  right: 0;
  padding: 0.5rem;
  font-size: smaller;
  font-weight: bold;
  z-index: 1;
  a:link, a:visited {
    color: inherit;
    text-decoration: none;
  }
  img {
    height: .8rem;
    margin-left: .2rem;
  }
}
