// Color variables
$color-font-primary: #333333; // font
$color-font-secondary: #666666; // Results 10 mail(s)
$color-font-bold: #111111; // sorted column header
$color-font-hover: #0033dd; // hover link
$color-bg-primary: #f5f5f5; // table header
$color-bg-secondary: #888888; // count
$color-border-primary: #dedede; // all the borders

// Variables
$border-primary: 1px solid $color-border-primary;

@mixin respond($breakpoint) {
  @if $breakpoint == phone {
    @media (max-width: 37.5em) {@content;} //600px
  }
  @if $breakpoint == tab-port {
    @media (max-width: 56.25em) {@content;} //900px
  }
  @if $breakpoint == tab-land {
    @media (max-width: 75em) {@content;} //1200px
  }
  @if $breakpoint == big-desktop {
    @media (min-width: 112.5em) {@content;} //1800px
  }
}