@import "../../share";

// Variables
$p: 0.5rem;
$h: 2rem;
$m: 25rem;

@include respond(phone) {
    $m: 15rem;
}

.window {
    border-radius: 7px 7px 0px 0px;
    min-width: 15rem;
    max-width: 23rem;
    flex: 1;
    z-index: 1000;
    @include respond(tab-land) {
        min-width: 12rem;
        max-width: 23rem;
        // color: red;
    }
    @include respond(phone) {
        min-width: 100%;
        max-width: 100%;
        max-height: 70vh;
        // color: yellowgreen;
    }

    &__header {
        border-radius: 7px 7px 0px 0px;
        border: $border-primary;
        padding: $p;
        height: $h;
        background-color: #666;
        overflow: hidden;
        display: flex;
        justify-content: left;
        align-items: center;
        flex-wrap: nowrap;
        font-weight: bold;
        color: white
    }

    &__subject {
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        flex: 1;
        // background-color: #fff;
    }

    &__maximize {
        padding-left: $p;
        // background-color: lawngreen;
    }

    &__close {
        padding-left: $p;
        // background-color: khaki;
    }

    &__main {
        border: $border-primary;
        height: $m;
        max-height: 70vh;
        padding: $p;
        overflow: scroll;
        overflow-y: auto;
        overflow-x: auto;
        // overflow-wrap: anywhere;
        background-color: #fff;
    }
    // &__date {
    // }
    // &__from {
    // }
    // &__to {
    // }
    // &__body {
    // }
    // &__attachment {
    // }
}

.window.maximized {
    @extend .window;
    transform: translateY(max(#{-$m}, -60vh));
}
